
const Noticia = (props) => {

     const {description, title, url, urlToImage, content, name} = props.noticia;
      
     let imagen = '';

     if ( urlToImage !== ''){
          imagen = <img src={urlToImage} alt={title} className="card-img-top" />
     }


     return(

        <div className="col-md-6 col-12 mb-4">
             <div className="card">
                  <div className="card-body">
                       <h4 className="card-title"> {title}</h4>
                  </div>
                <div className="card-image">
                     {imagen}
                     <p className="card-text">{description}</p>
                     <br/>
                      <button type="button" class="btn btn-warning btn-block"><a href={url} className="font-weight-bold white">See News</a></button>
                </div>

             </div>
        </div>
     );


}

export default Noticia;